import Axios from 'axios';
import fuzzysort from "fuzzysort";

const DB = 'http://localhost:3000/';
const headers = { 'content-type': 'application/json' }

export default class {

    constructor() {

    }
    listeDesVentes = async () => {
        return await Axios.get(DB+'sales').then(e => e.data)
    }

    detailVente = async (id) => {
        return await Axios.get(DB+`items?sale_id=${id}`).then(e => e.data)
    }

    nouvelleVente = async ({ vente, lots }) => {

        return await Axios.post(DB+'sales', JSON.stringify(vente), { headers })
            .then(async ({data}) => {

                await Promise.all(lots.map(lot => {
                    const lotJSON = JSON.stringify({ ...lot, sale_id: data.id });
                    return Axios.post(DB + `items`, lotJSON, { headers })
                }))

                return data
        })
    }

    rafraichitCatalogue = () => {
        Axios.get(DB+`db`).then(e => {
            this.catalogue = {
                objets: e.data.sales.map(sale => {

                    const items = e.data.items.filter(item => item.sale_id === sale.id);
                    const keys = items.reduce((acc, item) => ({
                        ...acc,
                        [`item_${item.id}`]: item.description
                    }), {})

                    return { ...sale, ...keys }
                }),
                keys: e.data.items.map(item => `item_${item.id}`)
            }
        })
    }

    lanceUneRecherche = (str) => {

        const options = {
            limit: 100, // don't return more results than you need!
            threshold: -10000, // don't return bad results
            keys: ['title', 'description', ...this.catalogue.keys ]
        }

        const res = fuzzysort.go(str, this.catalogue.objets, options)
            .map(e => ({
                obj: e.obj,
                content:e.map(objet => ({
                objet,
                html: fuzzysort.highlight(objet, '<b>', '</b>')
                }))
            }));

        console.log(this.catalogue)

        return res;
    }
}
