<template>
  <b-card bg-variant="light">
    <b-form-group
        label-cols-lg="3"
        label="Nouvelle vente"
        label-size="lg"
        label-class="font-weight-bold pt-0"
        class="mb-2"
    >
      <b-container fluid>
        <b-row class="mb-1">
          <b-col sm="2"><label for="titre">Titre:</label></b-col>
          <b-col sm="10">
            <b-form-input
                id="titre"
                v-model="vente.title"
                placeholder="Titre de la vente"
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mb-1">
          <b-col sm="2"><label for="description">Description:</label></b-col>
          <b-col sm="10">
            <b-form-textarea
                id="description"
                placeholder="Description de la vente"
                v-model="vente.description"
            ></b-form-textarea>
          </b-col>
        </b-row>
      </b-container>
    </b-form-group>
    <b-form-group
        label-cols-lg="3"
        label="Lots"
        label-size="lg"
        label-class="font-weight-bold pt-0"
        class="mb-2"
    >
      <b-container
          v-for="(lot, index) in lots"
          fluid
          :key="'lot_'+index"
      >
        <b-row class="mb-1">
          <b-col sm="2"><label for="description">Description:</label></b-col>
          <b-col sm="10">
            <b-form-textarea
                id="description"
                placeholder="Description du lot"
                v-model="lot.description"
                @inpit="$emit"
            ></b-form-textarea>
          </b-col>
        </b-row>
      </b-container>
      <b-container>
        <b-button @click="ajoutLot" class="float-right">Ajouter un lot</b-button>
      </b-container>
    </b-form-group>
    <b-form-group>
      <b-button @click="enregistrerVente({ vente, lots })">Créer la vente</b-button>
    </b-form-group>
  </b-card>
</template>

<script>
export default {
  name: "NouvelleVente",
  data: () => ({
    vente: { title: '', description: '' },
    lots: [{ description: '' }],
  }),
  methods: {
    ajoutLot () {
      this.lots.push({ description: '' });
    },
    async enregistrerVente (data) {
      await this.$emit('enregistrerVente', data);
    },
  },
}
</script>

