<template>
  <div id="app">
    <b-container>
      <b-row>
        <b-col cols="3">
          <ListeVentes
              :listeDesVentes="listeDesVentes"
              @changeDetailVente="changeDetailVente"
              @creeNouvelleVente="creeNouvelleVente"
              @rafraichitCatalogue="$maisonDeVente.rafraichitCatalogue()"
              @lanceUneRecherche="lanceUneRecherche"
          />
        </b-col>
        <b-col cols="9">
          <b-container v-if="resultatsRecherche.length">
            <b-row>
              <b-col
                  v-for="resultat in resultatsRecherche"
                  :key="'found_'+resultat.id"
                  cols="4"
              >
                <b-card no-body>
                  <b-card-header v-html="resultat.content[0].html && resultat.content[0].html.length ? resultat.content[0].html : resultat.obj.title" />
                  <b-card-body>
                    <p v-html="resultat.content[1].html && resultat.content[1].html.length ? resultat.content[1].html : resultat.obj.description"></p>
                  </b-card-body>
                    <b-list-group flush>
                      <template v-for="(vente, index) in resultat.content">
                        <b-list-group-item
                            href="#"
                            v-if="index > 1 && vente.html && vente.html.length"
                            v-html="vente.html"
                            :id="'vente_'+vente.id"
                            :key="'vente_'+vente.id"
                        />
                      </template>
                    </b-list-group>

                </b-card>
              </b-col>
            </b-row>
          </b-container>
          <DetailVente
              v-else-if="selectionActive.vente"
              :detailVente="selectionActive"
          />
          <NouvelleVente
              v-else-if="nouvelleVente"
              @enregistrerVente="enregistrerVente"
          />
          <span v-else>
            <h1>Bienvenue dans la maison de vente</h1>
            <p>Veuillez selectionner une vente </p>
          </span>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import ListeVentes from '@/components/ListeVentes'
import DetailVente from '@/components/DetailVente'
import NouvelleVente from "@/components/NouvelleVente"

export default {
  name: 'App',
  components: {
    ListeVentes, DetailVente, NouvelleVente
  },
  data: () => ({
    selectionActive: {},
    nouvelleVente: false,
    listeDesVentes: [],
    resultatsRecherche: [],
  }),
  methods: {
    creeNouvelleVente () {
      this.nouvelleVente = true;
      this.selectionActive = {};
      this.resultatsRecherche = [];
    },
    async changeDetailVente (vente) {
      this.resultatsRecherche = [];
      this.selectionActive = {
          vente,
          lots: await this.$maisonDeVente.detailVente(vente.id),
      }
    },
    async enregistrerVente (e) {
      this.$maisonDeVente.nouvelleVente(e).then(async (data) => {
        await this.chargeVentes();
        await this.changeDetailVente(data);
      });
    },
    async chargeVentes () {
      this.listeDesVentes = await this.$maisonDeVente.listeDesVentes();
    },
    async lanceUneRecherche (event) {
      this.resultatsRecherche = event.length > 2 ? this.$maisonDeVente.lanceUneRecherche(event) : [];
      console.log(this.resultatsRecherche)
    }
  },
  mounted () {
    this.chargeVentes();
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
