<template>
  <span>
    <b-card no-body header="Liste des ventes">
      <b-list-group flush>
        <template v-for="vente in listeDesVentes">
          <b-list-group-item
              href="#"
              v-b-tooltip.hover.right
              :title="vente.description"
              :id="'vente_'+vente.id"
              :key="'vente_'+vente.id"
              @click="$emit('changeDetailVente', vente)"
          >
            {{ vente.title }}
          </b-list-group-item>
        </template>
      </b-list-group>
      <b-card-body>
        <b-input-group>
          <template #prepend>
            <b-input-group-text class="icon-sm">
              <b-icon icon="search" aria-hidden="true"></b-icon>
            </b-input-group-text>
          </template>
            <b-form-input
                @focus="$emit('rafraichitCatalogue')"
                @input="$emit('lanceUneRecherche', $event)"
            ></b-form-input>
        </b-input-group>
      </b-card-body>
    </b-card>
    <b-button
        class="listeBtn"
        @click="$emit('creeNouvelleVente')"
    >
      Ajouter une vente
    </b-button>
    </span>
</template>

<script>
export default {
  name: 'ListeVentes',
  props: {
    listeDesVentes: Array
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.listeBtn {
  margin-top: 1em;
  width: 100%;
}
.icon-sm {
  font-size: 10px;
}
</style>
